<template>
    <div class="modal">
<div :class="['popup',{'on':isOn}]">
    <div class="container">
      <div class="title">
        <h1>{{title}}</h1>
      </div>
      <div class="content">
        <ul>
          <li v-for='item in 모달내용' :key="item">{{item}}</li>
        </ul>
      </div>
      <div class="button-box">
        <button @click.prevent="onClickPopupClose" class="close-btn" title="확인">확인</button>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import eventBus from '../../eventBus.js'
export default {
    name:'ModalComponent',
    props:[],
    data(){
        return{
            title:'시승신청 안내',
            모달내용:[
                'BMW코리아를 찾아주신 고객님께 안내말씀',
                '매장별로 시승 가능 차량모델이 상이할수 있으므로 해당 지점에 연락후 예약바랍니다'
            ],
            isOn: false
        }
    },
    created(){
      eventBus.$on('popupOpen', () => {
        this.isOn = true;
    });    
  },
    methods:{
        onClickPopupClose(){
            this.isOn = false;
        }
    }
}
</script>

<style>
#wrap .popup {
z-index: 101;
position: fixed;
top: 0;
left: 0;
width: 100%;
height: 100%;
background: rgba(0,0,0, 0.5);

/* 팝업창 가운데 */
display: flex;
justify-content: center;
align-items: center;
display: none;
}

#wrap .popup.on {
display: flex;
}

#wrap .popup .container {
width: 500px;
background: #fff;
border: 1px solid #ccc;
padding: 20px;
}

#wrap .popup .container .title {width:100%;}
#wrap .popup .container .title h1 {
font-size: 20px;
font-weight: 600;
color:#222;
border-bottom: 1px solid #ddd;
padding-bottom: 20px;
}

#wrap .popup .container .content {
padding-top: 20px;
}
#wrap .popup .container .content ul {width:100%;}
#wrap .popup .container .content ul li {width:100%;line-height: 180%;}

#wrap .popup .container .button-box {padding-top: 50px;text-align: right;}
#wrap .popup .container .button-box button {
width: 60px;
height: 35px;
border: 1px solid #ccc;
background: #fff;
color: #333;
cursor: pointer;   
border-radius: 3px; 
font-weight: 500;
}
#wrap .popup .container .button-box button:hover {
background: #007AC1;
color: #fff;
}
</style>